import React from 'react';
import styled from 'styled-components';
import { Link } from 'react-router-dom';
import FavoriteRoundedIcon from '@mui/icons-material/FavoriteRounded';

const media = (width) => `@media only screen and (max-width:${width}px)`;

const Parent = styled.div`
    color:rgba(255, 255, 255, 0.85);
    width:100%;
    background:#262626;
    padding-bottom:5px;
`;

const Hr = styled.hr`
    position:relative;
    width:60vw;
    float:left;
    margin-left:20px;
    margin-top:-20px;
    border:0.4px solid rgba(255, 255, 255, 0.5);
    ${media(600)}{
        width:80vw;
        margin:-10px auto 0 auto;
        float:none;
    }
`;

const Top = styled.p`
    padding-left:25px;
    font-size:40px;
    letter-spacing:6px;
    position:relative;
    span{
        display:block;
        max-width:65vw;
        margin-top:10px;
        font-size:small;
        vertical-align:bottom;
    }
    ${media(600)}{
        font-size:30px;
        text-align:center;
        padding:0;
        span{
            margin:10px auto;
        }
    }
`;

const FollowUs = styled.div`
    margin:35px auto 0 auto;
    text-align:center;
    display:flex;
    justify-content:center;
    align-items:center;
    *{
        transition:all 0.2s ease;
    }
    img{
        width:30px;
    }
    div{
        padding:10px;
    }
    a:hover img{
        transform:scale(1.2);
    }
`;

const Links = styled(Link)`
    padding:20px;
    display:block;
    color:white;
    text-align:center;
    margin:auto;
    width:fit-content;
    transition:all 0.2s ease-in-out;
    &:hover{
        transform: scale(1.1);
    }
    &.joinUs{
        border-bottom:5px solid white;
        margin-top:-5px;
        margin-bottom:20px;
        min-width:100px;
        svg{
            color:red;
            position:relative;
            top:7px;
        }
    }
`;

const FooterTemplate = (props) =>{
    return(
        <Parent style={props.style}>
            <Top>
                Samarpan&trade;
                <span className="desc">Starlight and moonlight review platform for newbies</span>
            </Top>
            <Hr />
            <Links to='/Categories'>Categories</Links>
            <Links to='/Movies'>Movies</Links>
            <Links to='/Anime'>Anime</Links>
            <Links to='/Series'>Series</Links>
            <Links to='/login' className="joinUs">Join Us <FavoriteRoundedIcon /> </Links>
            <FollowUs>
                <div>
                    Follow Us:
                </div>
                <div>
                    <a href='#'>
                        <img alt="svgImg" src="data:image/svg+xml;base64,"/>
                    </a>
                </div>
                <div>
                    <a href='https://github.com/Abdus8Samad'>
                        <img alt="svgImg" src="data:image/svg+xml;base64,"/>
                    </a>
                </div>
                <div>
                    <a href='#'>
                        <img alt="svgImg" src="data:image/svg+xml;base64,"/>
                    </a>
                </div>
                <div>
                    <a href='#'>
                        <img alt="svgImg" src="data:image/svg+xml;base64,"/>
                    </a>
                </div>
            </FollowUs>
        </Parent>
    )
}

const FooterContainer = styled.div`
    position:relative;
`;

const Front = {
    "position":"fixed",
    "bottom":"0",
    "zIndex":"20"
};

const Back = {
    "position":"relative",
    "visibility":"hidden",
    "zIndex":"-20"
};

const Footer = () =>{
    return(
        <FooterContainer>
            <FooterTemplate style={Front}/>
            <FooterTemplate style={Back}/>
        </FooterContainer>
    )
}

export default Footer;